#search-wrapper {
  -webkit-align-items: center;
      align-items: center;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  /* display: inline-flex; */
  -webkit-flex-flow: row-reverse nowrap;
          flex-flow: row-reverse nowrap;
  width: 1250px;
  height: 40px;
}
#search-wrapper #search {
  background-color: #f2f2f2;
  border: 1px ;
  border-left: 0;
  border-radius: 0 4px 4px 0;
  color: #444;
  padding: .6rem .5rem;
  font-family: 'Lato';
  font-size: 15px;
  font-weight: lighter;
  transition: border-color .2s ease;
  width: 100%;
}
#search-wrapper #search + i {
  background-color: #f2f2f2;
  border: 1px ;
  border-radius: 4px 0 0 4px;
  border-right: 0;
  color: #777;
  padding: .67rem .65rem;
  transition: border-color .2s ease;
}
#search-wrapper #search:focus {
  border-color: #999;
  outline: none;
}
#search-wrapper #search:focus + i {
  border-color: #999;
  font-size: 20px;
}
#search-wrapper ul li {
    display: inline;
    margin: 17px;
    font-size: 16px;
}
#search-wrapper ul li i{
    display: inline;
    margin: 2px;
    font-size: 24px;
    font-weight: 700;
    color:#8e8e8e;
}
input::-webkit-input-placeholder { 
